.ProfileCard{
    border-radius: 1.5rem;
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 1rem;
    overflow-x: clip;
    background: var(--cardColor);
}

.ProfileImages{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ProfileImages>img:nth-of-type(1){
 width: 100%;
 
}
.ProfileImages>img:nth-of-type(2){
 width: 6rem;
 border-radius: 50%;
 position: absolute;
bottom: -3rem;
box-shadow: var(--profileShadow);

}
.ProfileName{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    gap: 10px;
}

.ProfileName>span:nth-of-type(1){
    font-weight: bold;
}

/* follow status */

.followStatus{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: .75rem;
}

.followStatus>hr{
    width: 85%;
    border: 1px solid var(--hrColor);
}


.followStatus >div{
    display: flex;
    gap: 1rem;
    width: 80%;
    justify-content: space-around;
    align-items: center;
}

.follow{
    display: flex;
    flex-direction: column;
    gap: .4rem;

    justify-content: center;
    align-items: center;  
}

.follow>span:nth-of-type(1){
    font-weight: bold;
}
.follow>span:nth-of-type(2){
   color: var(--gray);
   font-size: 15px;
}

.vl{
    height: 150%;
    border-left: 2px solid var(--hrColor);
}

.ProfileCard>span{
    color: orange;
    font-weight: bold;
    align-self: center;
    margin-bottom: 1rem;
    cursor: pointer;
}